import {STORAGE_KEYS} from '../util/constants';
import {useMediaQuery, useStickyState} from '../util/hooks';
import {applyTheme} from '../util/util';
import React, {createContext, useEffect, useCallback} from 'react';

export const ThemeContext = createContext();

const ThemeProvider = ({children}) => {
	const [theme, setTheme] = useStickyState('system', STORAGE_KEYS.theme);

	const setAndApplyTheme = useCallback(
		theme => {
			setTheme(theme);
			applyTheme(theme);
		},
		[setTheme]
	);

	useEffect(() => {
		const root = window.document.documentElement;
		const initialTheme = root.style.getPropertyValue('--theme');
		setAndApplyTheme(initialTheme);
	}, []);

	const systemTheme = useMediaQuery('(prefers-color-scheme: dark)')
		? 'dark'
		: 'light';
	const perceivedTheme = theme === 'system' ? systemTheme : theme;

	return (
		<ThemeContext.Provider
			value={{theme, perceivedTheme, setTheme: setAndApplyTheme}}
		>
			{children}
		</ThemeContext.Provider>
	);
};

export default ThemeProvider;
